.App {
  font-family: sans-serif;
  text-align: center;
  background-color: black;
}

body {
  background-color: black;
  height: 100vh;
}

html *,
html {
  cursor: none;
}

.cursor {
  position: fixed;
  background: red;
  opacity: 0.5;
  width: 12px;
  height: 12px;
  margin: -6px;
  border-radius: 50%;
  will-change: transform;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  z-index: 200;
}

.font-rm {
  font-family: "Roboto Mono", monospace !important;
}
